<template>
  <div class="pageContent">
    <div v-if="noAttestation"></div>
    <el-card v-else class="filter-container" shadow="never">
      <div class="pageHead flex-row-between">
        <div>店铺余额提现</div>
        <el-button type="text" @click="$router.push({path:'/assetsManage/account/record',query:{type:2}})">查看提现记录</el-button>
      </div>
      <el-form ref="form" :model="form" label-width="160px">
        <el-form-item label="可提现金额：">
          <div class="warningFont fs40">{{form.sumAmount}}</div>
        </el-form-item>
        <el-form-item label="银行卡：">
          <el-select v-model="form.bankCode" disabled>
            <el-option :label="form.openName + ' ' +form.bankAccount" :value="form.bankCode"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="提现金额：">
          <el-input-number 
            style="width:200px;" 
            v-model="form.withdrawAmount" 
            :controls="false"
            placeholder="最多可输入5000"
          step-strictly></el-input-number>
          <!-- </el-input-number> 元 -->
          <el-button style="margin-left:10px;" type="text" @click="allCash">全额提出</el-button>
          <div class="dangerFont">提现会扣取对应的手续费，大约在1‰-2‰，具体金额可在提现记录或交易记录里查看</div>
        </el-form-item>
        <el-form-item label="手机号验证：">
          <el-input v-model="form.name" style="width:200px;"></el-input>
        </el-form-item>
        <el-form-item label="验证码：">
          <el-input v-model="form.name" style="width:200px;"></el-input>
          <el-button type="primary">获取验证码</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">确定提现</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-dialog title="提现申请已提交，请耐心等待！" :visible.sync="rechargeShow" width="30%" center :append-to-body="true">
      <div class="tac">
        <p>由于不同银行处理时间不同，大约在1-7个工作日到账。</p>
        <p>温馨提示：金额大于5000的，需筷赚财务审核。</p>
      </div>
      <span slot="footer" class="dialog-footer">
        <div>
          <el-button type="primary" style="width:160px;" @click="rechargeShow = false">好的</el-button>
        </div>
        <div class="mt10">
          <el-button type="text" @click="$router.push({path:'/assetsManage/account/record',query:{type:2}})">查看提现记录</el-button>
        </div>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopWithdrawAmount , shopApplyWithdraw } from '@/api/assetsManage/verifyFund'
import { getShopAuthInfoByShopId } from '@/api/shop/shopAttestation'
export default {
  data(){
    return{
      form:{
        resource:'1',
      },
      rechargeShow:false,         // 充值弹出框
      noAttestation:true
    }
  },
  created(){
    this.form.shopId = this.shop.shopId
    this.form.userId = this.user.userId
    this.getData()
  },
  methods:{
    getData(){
      getShopAuthInfoByShopId(this.shop.shopId).then(res=>{
        console.log('店铺认证',res)
        if(res.data == null){
          console.log('店铺未认证')
          this.noAttestation = true;
          this.$confirm('店铺主体未认证，暂时无法使用提现功能', '提示', {
            confirmButtonText: '切换店铺',
            cancelButtonText: '去认证',
            showClose:false,
            distinguishCancelAndClose:true,
            closeOnClickModal:false,
            closeOnPressEscap:false,
            type: 'warning'
          }).then(() => {
            console.log('点击了切换店铺')
            this.$router.push({path:'/selectShop'})
          }).catch(() => {
            console.log('点击了去认证')        
            this.$router.push({
              path:'/shop/shopAttestation'
            })
          });
        }else{
          this.noAttestation = false;
          getShopWithdrawAmount(this.shop.shopId).then(res1=>{
            console.log('店铺余额数据',res1)
            if(res1.code == 200){
              // res1.data.bankCode =res1.data.bankCode + ''
              // res1.data.bankCode = Number(res1.data.bankCode)
              res1.data.withdrawAmount = ''
              this.form = res1.data;
            }
          })
        }
      })
    },
    // 点击提现
    onSubmit(){
      if(!this.form.withdrawAmount){
        this.$message.error('请输入金额后再试');
        return;
      }
      shopApplyWithdraw(this.form).then(res=>{
        console.log('提现操作',res)
        if(res.code == 200){
          this.rechargeShow = true;
        }else{
          this.$message.error(res.message);
        }
      })
    },
    allCash(){
      console.log('this.form',this.form)
      this.form.withdrawAmount = this.form.sumAmount > 5000 ? 5000 : this.form.sumAmount;
      // this.form.withdrawAmount = 1;
    },
    change (e) {
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
.fs40{
  font-size: 40px;
  font-weight: bold;
}
</style>